
import { mapGetters,mapActions }from 'vuex'
import wx from 'weixin-js-sdk'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Picker from 'vux/src/components/picker/index.vue'

export default {
data(){
return{
slected:[''],
menus1:[[{name:'',value:''}]],
show1:false,
fgp:true,
shTYpe:'',
grandTotal:'7日无理由',
defaultValue:'',
list:[],
file:null,
srcArr:[],
contentid:[],
feedbackContent:'',
dec:''
}
},
created(){
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}
this.showLoading()
this.getReturnOrderReason({this:this,data:{productId:this.$route.query.csi,orderId:this.$route.query.id},method:this.getReturnOrderReason})

},
mounted(){
document.title = '申请售后'
},
methods:{
...mapActions(['personContentUpload','createFeedback','getReturnOrderReason','physicalRefund']),
getdec(){
let obj = this.menus1[0].filter(v => {
return v.value == this.slected[0];
});
this.dec =  obj[0]?obj[0].name:''
},
goCe(){
if(!this.slected[0]){
this.slected[0] = this.menus1[0][0].value
}
this.defaultValue  = this.slected[0]
this.show1 = false
this.getdec()
},
doMe(){



this.$router.replace('/afterSaleList')
},
cgels(){
if( this.grandTotal.indexOf('.')>-1){
this.grandTotal= this.grandTotal.slice(0,this.grandTotal.indexOf('.')+3)
}
},
sunb(){
if(!this.shTYpe){
this.showTost('请选择退货类型')
return
}
if(!this.grandTotal){
this.showTost('请填写退款金额')
return
}
if(!this.defaultValue){
this.showTost('请选择退货原因')
return
}
if(this.feedbackContent&&this.feedbackContent.length>200){
this.showTost('问题描述最多可输入200字符')
return
}
let obj={}
if(this.shTYpe == 2){
obj.refundType ='product'
}
if(this.shTYpe == 1){
obj.refundType =''
}
obj.productId = this.$route.query.csi
obj.orderId = this.$route.query.id
obj.refundMoney = this.grandTotal
obj.refundReason = this.feedbackContent
obj.contentIds = this.contentid.join(',')
obj.enumId = this.defaultValue
this.showLoading()
this.physicalRefund({this:this,data:obj,method:this.physicalRefund})
},
getData(obj){
if(obj.reasonList&&obj.reasonList.length){
let data = obj.reasonList.map(v=>{
return {value:v.enumId,name:v.description}
})
this.menus1 = [data]
}



this.grandTotal = obj.grandTotal
},
coseImg(){

},
getContentid(id){
this.contentid.push(id)
},
convertBase64UrlToBlob(urlData){
let arr = urlData.split(','), mime = arr[0].match(/:(.*?);/)[1],
bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
while(n--){
u8arr[n] = bstr.charCodeAt(n);
}
return new Blob([u8arr], {type:mime});
},
uploading(event){
let _this = this
this.file = event.target.files[0];
var windowURL = window.URL || window.webkitURL;
this.srcArr.push(windowURL.createObjectURL(event.target.files[0]))
if(this.file.size > 1024 * 1024){//大于1m压缩
this.compress(this.file,function(data){
var bl = _this.convertBase64UrlToBlob(data);
let formdata = new FormData();

formdata.append("uploadedFile", bl, "file_"+Date.parse(new Date())+".jpg"); 
formdata.append('localeString','zh');
formdata.append('partyContentTypeId','PARTY_IMG_CONTENT');
_this.showLoading()
_this.personContentUpload({data:formdata,this:_this,method:_this.personContentUpload})

})
}
else{
this.showLoading()
this.sunmitd()
}
},
compress(obj,callback){
let _this = this
var reader = new FileReader();
reader.readAsDataURL(this.file);
reader.onload = function() {

return   _this.canvasDataURL(this.result,callback)

}
},
canvasDataURL(path,callback){
let img = new Image();
img.src = path;
img.onload = function(){
let that = this;

let w = that.width,
h = that.height,
scale = w / h;
let quality = 0.5;  

var canvas = document.createElement('canvas');
var ctx = canvas.getContext('2d');

var anw = document.createAttribute("width");
anw.nodeValue = w;
var anh = document.createAttribute("height");
anh.nodeValue = h;
canvas.setAttributeNode(anw);
canvas.setAttributeNode(anh);
ctx.drawImage(that, 0, 0, w, h);

var base64 = canvas.toDataURL('image/jpeg', quality);

callback(base64);
}
},
del(i){
this.srcArr.splice(i,1)
this.contentid.splice(i,1)
},
sunmitd(){
let formdata = new FormData();
formdata.append('uploadedFile',this.file);
formdata.append('localeString','zh');
formdata.append('partyContentTypeId','PARTY_IMG_CONTENT');
this.personContentUpload({data:formdata,this:this})
},
onEvent (event) {
if(event === 'focus'){
this.fgp = true
document.getElementsByClassName('alionfr')[0].scrollTop = 100
}
else{
this.fgp = false
}
}
},
components:{
XTextarea,Group,Selector,Picker
}
}



